<style lang="scss"  scoped>
.cardDivInner h1{
    font-family: 'Pacifico', cursive;
}



#checklist {

    --check: #4F29F0;
    --disabled: #C3C8DE;
    --width: 100px;
    --height: 140px;
    --border-radius: 10px;
    position: relative;
    label {
        color: var(--text);
        position: relative;
        display: grid;
        align-items: center;
        transition: color .3s ease;
        &::before,
        &::after {
            content:"";
            position: absolute;
        }
        &::before {
            height: 2px;
            width: 8px;
            left: -27px;
            background: var(--check);
            border-radius: 2px;
            transition: background .3s ease; 
        }
        &:after {
            height: 4px;
            width: 4px;
            top: 8px;
            left: -25px;
            border-radius: 50%;
        }
    }
    input[type="checkbox"] {
        -webkit-appearance: none;
        -moz-appearance: none;
        position: relative;
        margin: 8px 15px 0 0;
        background: var(--background);
        &::before,
        &::after {
            content:"";
            position: absolute;
            height: 2px;
            top: 50%;
            background: var(--check);
            border-radius: 2px;
        }
        &::before {
            width: 0px;
            right: 60%;
            transform-origin: right bottom;
        }
        &::after {
            width: 0px;
            left: 40%;
            transform-origin: left bottom;
        }
        &:checked {
            &::before {
                animation: check-01 .4s ease forwards;
            }
            &::after {
                animation: check-02 .4s ease forwards;
            }
            + label {
                color: var(--disabled);
                animation: move .3s ease .1s forwards;
                &::before {
                    background: var(--disabled);
                    animation: slice .4s ease forwards;
                }
                &::after {
                    animation: firework .5s ease forwards .1s;
                }
            }
        }
    }
}

    @keyframes move {
    50% {
    padding-left: 8px;
    padding-right: 0px;
    }
    100% {
    padding-right: 4px;
    }
    }
    @keyframes slice {
    60% {
    width: 100%;
    left: 4px;
    }
    100% {
    width: 100%;
    left: -2px;
    padding-left: 0;
    }
    }
    @keyframes check-01 {
    0% {
    width: 4px;
    top: auto;
    transform: rotate(0);
    }
    50% {
    width: 0px;
    top: auto;
    transform: rotate(0);
    }
    51% {
    width: 0px;
    top: 4px;
    transform: rotate(45deg);
    }
    100% {
    width: 5px;
    top: 4px;
    transform: rotate(45deg);
    }
    }
    @keyframes check-02 {
    0% {
    width: 4px;
    top: auto;
    transform: rotate(0);
    }
    50% {
    width: 0px;
    top: auto;
    transform: rotate(0);
    }
    51% {
    width: 0px;
    top: 4px;
    transform: rotate(-45deg);
    }
    100% {
    width: 10px;
    top: 4px;
    transform: rotate(-45deg);
    }
    }
    @keyframes firework {
    0% {
    opacity: 1;
    box-shadow: 0 0 0 -2px #4F29F0, 0 0 0 -2px #4F29F0, 0 0 0 -2px #4F29F0, 0 0 0 -2px #4F29F0, 0 0 0 -2px #4F29F0, 0 0 0 -2px #4F29F0;
    }
    30% {
    opacity: 1;
    }
    100% {
    opacity: 0;
    box-shadow: 0 -15px 0 0px #4F29F0, 14px -8px 0 0px #4F29F0, 14px 8px 0 0px #4F29F0, 0 15px 0 0px #4F29F0, -14px 8px 0 0px #4F29F0, -14px -8px 0 0px #4F29F0;
    }
}

</style>